import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import _ from 'lodash';

import Search from './Search.js';
import Video from './Video-item.js';
import {
  getVideosFromYouTube as _getVideosFromYouTube,
  changeIndex as _changeIndex
 } from '../actions/video.js';

import eye from '../assets/images/eye.png';
import checked from '../assets/images/checked.png';
import checked_white from '../assets/images/checked-white.png';
import person from '../assets/images/person.png';
import profile_img from '../assets/images/profile_img.png';
import social from '../assets/images/social.png';
import phone from '../assets/images/phone.png';
import envelope from '../assets/images/envelope.png';
import star from '../assets/images/star.png';
import star_white from '../assets/images/star-white.png';

class Slider extends Component {

  state = {
    isPopup: false
  }

  showPopup = () => {
    this.setState({ isPopup: true });
    setTimeout(() => {this.setState({ isPopup: false });}, 1000);
  }

  render() {

    return (
      <div className='wrapper'>

        {
          this.state.isPopup &&
          <div className='popup-editable'>
            {'Will be editable'}
          </div>
        }

        <div className='header'>
          <div className='logo'>
            <div className='logo-cv'>{'CV'}</div>
            <div className='logo-gen'>{'GEN'}</div>
          </div>
          <div className='preview-container'>
            <div className='preview'>
              <div className='eye-container'>
                <img src={eye} alt='' />
              </div>
              <div className='preview-title'>
                {'PREVIEW'}
              </div>
            </div>
          </div>
          <div className='progress-container'>
            <div className='progress-bar'></div>
            <div className='checked'>
              <img src={checked} alt='' />
            </div>
          </div>
          <div className='cv-afronden-container'>
            <div className='checked-white'>
              <img src={checked_white} alt='' />
            </div>
            <div className='cv-afronden-title'>{'CV AFRONDEN'}</div>
          </div>
          <div className='vertical-line'></div>
          <div className='person-container'>
            <div className='person-img'>
              <img src={person} alt='' />
            </div>
            <div className='person-titles'>
              <div className='person-title'>{'Bas Van Boom'}</div>
              <div className='person-subtitle'>{`4 gegenereerde cv's`}</div>
            </div>
            <div className='arrow'>
              <div className="arrow-up"></div>
                <div className="arrow-down"></div>
                <div className="arrow-left"></div>
                <div className="arrow-right"></div>
            </div>
          </div>
        </div>
        <div className='person-info-container'>
          <div className='person-info-img'>
            <img src={profile_img} alt='' />
          </div>
          <div className='person-info-data'>
            <div className='person-info-text-wrapper'>
              <div className='person-info-text'>
                <div className='name' onClick={this.showPopup}>{'Bas Van Boom'}</div>
                <div className='job-position' onClick={this.showPopup}>{'Web developer'}</div>
                <div className='date' onClick={this.showPopup}>{'1 januari 1990'}</div>
                <div className='address' onClick={this.showPopup}>{'Scheerstraat 12'}</div>
                <div className='city' onClick={this.showPopup}>{'4580AA Baardenweg'}</div>
              </div>
              <div className='person-phone-email-wrapper'>
                <div className='person-phone-email-container'>
                  <div className='phone-email-container'>
                    <div className='phone-email-icon'>
                      <img src={phone} alt='' />
                    </div>
                    <div className='phone-email-text' onClick={this.showPopup}>{'(+31)06 31 69 08 97'}</div>
                  </div>
                  <div className='phone-email-container'>
                    <div className='phone-email-icon'>
                      <img src={envelope} alt='' />
                    </div>
                    <div className='phone-email-text' onClick={this.showPopup}>{'basvanboom@gmail.com'}</div>
                  </div>
                </div>
              </div>
            </div>
            <div className='social-networks-container'>
              <img src={social} alt='' />
            </div>
          </div>
        </div>
        <div className='experience-skills-container'>
          <div className='experience-container'>
            <div className='experience-list-container'>
              <div className='experience-title'>{'Opleidingen'}</div>
              <div className='experience-list'>
                <div className='skill-item'>
                  <div className='skill-item-title'>
                    <div className='skill-title-container'>
                      <div className='position'>{'Elektro techniek'}</div>
                      <div className='organization'>{'Hogeschool Utrecht'}</div>
                    </div>
                  </div>
                  <div className='skill-item-date'>{'1998-2002'}</div>
                </div>
                <div className='skill-item'>
                  <div className='skill-item-title'>
                    <div className='skill-title-container'>
                      <div className='position'>{'Elektro techniek'}</div>
                      <div className='organization'>{'Hogeschool Utrecht'}</div>
                    </div>
                  </div>
                  <div className='skill-item-date'>{'1998-2002'}</div>
                </div>
                <div className='skill-item'>
                  <div className='skill-item-title'>
                    <div className='skill-title-container'>
                      <div className='position'>{'Elektro techniek'}</div>
                      <div className='organization'>{'Hogeschool Utrecht'}</div>
                    </div>
                  </div>
                  <div className='skill-item-date'>{'1998-2002'}</div>
                </div>
              </div>
            </div>
          </div>
          <div className='experience-container'>
            <div className='experience-list-container skills'>
              <div className='experience-title'>{'Skills'}</div>
              <div className='experience-list'>
                <div className='skill-item'>
                  <div className='skill-item-title right'>
                    <div className='skill-title-container'>
                      <div className='main-skill'>{'Photoshop'}</div>
                    </div>
                  </div>
                  <div className='skill-item-date right'>
                    <img src={star} alt='' />
                    <img src={star} alt='' />
                    <img src={star} alt='' />
                    <img src={star_white} alt='' />
                    <img src={star_white} alt='' />
                  </div>
                </div>
                <div className='skill-item'>
                  <div className='skill-item-title right'>
                    <div className='skill-title-container'>
                      <div className='main-skill'>{'Illustrator'}</div>
                    </div>
                  </div>
                  <div className='skill-item-date right'>
                    <img src={star} alt='' />
                    <img src={star} alt='' />
                    <img src={star} alt='' />
                    <img src={star} alt='' />
                    <img src={star_white} alt='' />
                  </div>
                </div>
                <div className='skill-item'>
                  <div className='skill-item-title right'>
                    <div className='skill-title-container'>
                      <div className='main-skill'>{'Dreamweaver'}</div>
                    </div>
                  </div>
                  <div className='skill-item-date right'>
                    <img src={star} alt='' />
                    <img src={star} alt='' />
                    <img src={star_white} alt='' />
                    <img src={star_white} alt='' />
                    <img src={star_white} alt='' />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}


const mapStateToProps = ({ }) => ({
  // videos,
  // currentVideoIndex,
});

const actions = {
  // _getVideosFromYouTube,
  // _changeIndex,
};

export default connect(mapStateToProps, actions)(Slider);



